.image-container{
    background-color: #fff;
    height: 100%;
    justify-content: center;
    align-items: center;
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 4 images per row */
    grid-template-rows: repeat(3, auto); /* 3 rows */

    gap: 0vw; /* Gap between images */
    
}

.image-container img {
    width: 17vh; /* Set a fixed width for images */
    height: 17vh; /* Set a fixed height for images */
    border-radius: 50%; /* Make the images circular */
    border: 1.5px solid #bfd1d8;
    object-fit: contain; /* Maintain aspect ratio while filling the container */
    padding: 1vw; 
    display: block; /* Change the display property */
    margin: 0 auto;
    opacity: 0;/*  Initially set opacity to 0 */
    animation: fadeIn 1s forwards;
}

.image-container img:nth-child(1) {
    animation-delay: 0.3s; /* Delay for the first image */
  }

.image-container img:nth-child(2) {
animation-delay: 0.6s; /* Delay for the first image */
}

.image-container img:nth-child(3) {
animation-delay: 0.9s; /* Delay for the first image */
}
